body,
html {
    padding: 0;
    margin: 0;
    background: #b39ddb;
}
.header {
    padding: 16px 16px;
    background: #7e57c2;
}
.question-div {
    position: absolute;
    top: 50%;
    left: 50%;
    width: auto;
    width: 800px;
    transform: translate(-50%, -50%);
    padding: 24px 24px;
    background: #7e57c2;
}
.question {
    margin-bottom: 18px;
    font-size: 24px;
    color: white;
    text-align: center;
}
.answers {
    width: 100%;
    height: auto;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
}
.answers div {
    width: 35%;
    margin: 8px;
    background: #b39ddb;
    padding: 8px 16px;
    display: flex;
    height: 36px;
    cursor: pointer;
    align-items: Center;
    justify-content: center;
    color: white;
    font-weight: bold;
    border-radius: 8px;
}
.user-header-text {
    color: white;
    font-size: 20px;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 500;
}
.start-quiz {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 34px;
    font-weight: 700;

    cursor: pointer;
}
.quiz {
    width: 300px;
    height: 300px;
    margin: 4px;
    display: flex;
    align-items: center;
    background-size: 100% 100%;
    position: relative;

    font-size: 24px;
    justify-content: center;
    cursor: pointer;
}
.quiz-title {
    z-index: 1;
    color: white;
    text-decoration: none;
}
.quiz:before {
    content: '';
    opacity: 0.3;
    background: black;
    position: absolute;
    width: 100%;
    height: 100%;
}
.quiz-selection {
    display: flex;
    flex-flow: row-wrap;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.start-quiz a {
    text-decoration: none;
    color: white;
}
